import React from "react";
import { useRouter } from "next/navigation";
import ResourcesList from "../../components/ResourcesList";

const ModulePage = () => {
  const router = useRouter();

  // Ejemplo de datos de recursos
  const resources = [
    { id: 1, name: "Recurso 1" },
    { id: 2, name: "Recurso 2" },
  ];

  return (
    <div className="min-h-screen bg-background-1 flex flex-col p-6">
      {/* Botón de Volver */}
      <button
        className="text-primary flex items-center gap-2 mb-6 hover:underline"
        onClick={() => router.push("/")}
      >
        <span className="material-icons">arrow_back</span>
        <span>Volver al Inicio</span>
      </button>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Columna principal */}
        <div className="md:col-span-2 space-y-4">
          <h1 className="text-3xl font-bold text-primary">Módulo 1</h1>
          <h2 className="text-xl text-neutral-600">Subtítulo del módulo</h2>
          <div className="w-full aspect-video bg-black">
            {/* Aquí puedes incrustar el iframe del video */}
          </div>
          <p className="text-neutral-700">
            Este es un módulo sobre un tema importante del nivel.
          </p>
        </div>

        {/* Recursos adicionales como componente */}
        <ResourcesList resources={resources} />
      </div>
    </div>
  );
};

export default ModulePage;
