import React from "react";
import { useRouter } from "next/navigation";

interface Resource {
  id: number;
  name: string;
}

interface ResourcesListProps {
  resources: Resource[];
}

const ResourcesList: React.FC<ResourcesListProps> = ({ resources }) => {
  const router = useRouter();

  return (
    <div className="bg-white shadow-lg p-4 rounded-lg">
      <h3 className="text-lg font-semibold text-primary">Recursos</h3>
      <ul className="space-y-2">
        {resources.map((resource) => (
          <li key={resource.id}>
            <a
              href="#"
              className="text-neutral-600 hover:text-primary underline"
            >
              {resource.name}
            </a>
          </li>
        ))}
      </ul>
      <button
        onClick={() => router.push("/resources")}
        className="text-sm mt-4 text-primary hover:underline"
      >
        Ver todo
      </button>
    </div>
  );
};

export default ResourcesList;
